import { Component, OnInit, Input, Output, EventEmitter, ElementRef, ViewChild, Renderer2, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { ViewChangeService } from './view-change.service';

@Component({
  selector: 'farris-view-change',
  templateUrl: './view-change.component.html',
  styleUrls: ['./view-change.component.scss'],
  encapsulation: ViewEncapsulation.None
  // providers:[ ViewChangeService ]
})
export class ViewChangeComponent implements OnInit ,AfterViewInit{

  @Input() toolbarData;
  @Input() viewType: string = 'tile';
  @Input() defaultType: string;
  @Input() viewGroupId:string = 'default';
  activeTypeItem;

  @Output() toolTypeChange = new EventEmitter();

  @ViewChild('typelist') typelist: ElementRef;

  constructor(private _renderer: Renderer2,
    private viewChangeService: ViewChangeService) { }

  ngOnInit() {
    if (this.toolbarData && this.toolbarData.length) {
      if (this.defaultType) {
        let item = this.toolbarData.find((bar) => {
          return bar['type'] === this.defaultType;
        })
        this.activeTypeItem = item;
        this.toolTypeChange.emit(this.activeTypeItem);
      }
      else {
        this.activeTypeItem = this.toolbarData[0];
        this.toolTypeChange.emit(this.activeTypeItem);
      }

      
    }
  }
  ngAfterViewInit(): void {
    if(!this.viewChangeService){
      this.viewChangeService = new ViewChangeService();
    }
    this.viewChangeService.updateCurrentViewId(this.activeTypeItem['type'],this.viewGroupId);
  }

  listItemClick(item) {
    if (item.disable || this.activeTypeItem['type'] === item['type']) {
      return;
    }
    this.activeTypeItem = item;
    if(!this.viewChangeService){
      this.viewChangeService = new ViewChangeService();
    }
    this.viewChangeService.updateCurrentViewId(this.activeTypeItem['type'],this.viewGroupId);
    this.toolTypeChange.emit(item);
    if (this.typelist) {
      this._renderer.setStyle(
        this.typelist.nativeElement,
        'display',
        'none'
      );
    }
  }

  dropdownMouseEnter() {
    this._renderer.setStyle(
      this.typelist.nativeElement,
      'display',
      'block'
    );
  }
  dropdownMouseLeave() {
    this._renderer.setStyle(
      this.typelist.nativeElement,
      'display',
      'none'
    );
  }

}
